import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AccountBlockingComponent } from './account-blocking.component';
import { AccountCustomerSearchComponent } from './account-customer-search/account-customer-search.component';

export const blockroutes: Routes = [
  {
    path: '',
    component: AccountBlockingComponent,
  },
  {
    path: 'customer-search',
    component: AccountCustomerSearchComponent,
    pathMatch: 'full',
    data: { title: 'Account Blocking' }
  }
];

