import { NgModule } from '@angular/core';
import { QuillModule } from 'ngx-quill';
import { CommonModule } from '@angular/common';

import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AngularMaterialModule } from 'src/app/shared/angular-material/angular-material.module';
import { FlexLayoutModule } from '@angular/flex-layout';
import { RouterModule } from '@angular/router';
import { AccountBlockingComponent } from './account-blocking.component';
import { blockroutes } from './account-blocking-routing.module';
import { NgxSpinnerModule } from 'ngx-spinner';
import { AccountCustomerSearchComponent } from './account-customer-search/account-customer-search.component';
import { ViewOnAccountBlockingComponent } from './view-on-account-blocking/view-on-account-blocking.component';
import { DataService } from './account-customer-search/dataservice';


@NgModule({
  declarations: [AccountBlockingComponent, AccountCustomerSearchComponent, ViewOnAccountBlockingComponent],
  imports: [
    CommonModule,
    FormsModule,

    AngularMaterialModule,
    NgxSpinnerModule,
    FlexLayoutModule,
    ReactiveFormsModule,
    RouterModule.forChild(blockroutes)
  ],
  entryComponents: [ViewOnAccountBlockingComponent],
  providers: [DataService]
})
export class AccountBlockingModule { }
