import { Component, OnInit } from '@angular/core';
import { sendMailToOperationTeam } from './AccountBlocking';
import swal from 'sweetalert2';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import Swal from 'sweetalert2';
import { AccountBlockingServiceService } from 'src/app/shared/services/account-blocking-service.service';
import { NavigationExtras, Router, ActivatedRoute } from '@angular/router';
import { FCUBSAccsCustomerQueryResDTO } from './account-customer-search/FCUBSAccsCustomerQueryResDTO';
import { DataService } from './account-customer-search/dataservice';

@Component({
  selector: 'app-account-blocking',
  templateUrl: './account-blocking.component.html',
  styleUrls: ['./account-blocking.component.css']
})
export class AccountBlockingComponent implements OnInit {
  sendMailtoOpt: sendMailToOperationTeam = new sendMailToOperationTeam();
  currentUser: string;
  data: any;
  resp: any;
  customerdatafromcomponent: any;
  respBackend: boolean;
  submitdisable: boolean;
  checkValue= true;
  constructor(private formBuilder: FormBuilder, private accountBlockingService: AccountBlockingServiceService,
              private router: Router, private dataService: DataService,      private route: ActivatedRoute) { }
  phoneNumber = '^(\+\d{1,3}[- ]?)?\d{10}$';
  accountBlockForm: FormGroup;
  accountBlock = new FCUBSAccsCustomerQueryResDTO();

  ngOnInit() {
    this.currentUser = localStorage.getItem('currentUser');
    this.dataService.getNewUserInfo().subscribe(customerdatafromcomponent => {
     this.customerdatafromcomponent = customerdatafromcomponent;
     //console.log(this.customerdatafromcomponent);
   });
   // console.log(this.customerdatafromcomponent);
    this.accountBlockForm = this.formBuilder.group({
      customerName: [''],
      customerNumber: [''],
      customerAccountNumber: [''],
      branchCode: [''],
      nameOfOperator: [this.currentUser],
      nameOfCustomer: [''],
      customerMobileNumber: [''],  // , Validators.pattern('^[0-9 \-\']+')  //, [Validators.required, Validators.minLength(10), Validators.maxLength(15)]
      mobileNumber: [''], // , Validators.pattern('^[0-9 \-\']+')  //, Validators.minLength(10), Validators.maxLength(15)]
      dueDeligenceInfo: [''],
      reasonForBlocking: [''],
      commentsByOperator: [''],
      emailid: ['', ],
      sendmail: ['', Validators.required]
     // dateforblockaccount: []
    });

    this.route.queryParams.subscribe(params => {
      this.accountBlock.firstName = this.customerdatafromcomponent.firstname;
     // console.log(this.accountBlock.firstName );
      this.accountBlock.lastName = this.customerdatafromcomponent.lastname;
      // console.log(this.accountBlock.lastName);
      if(!this.accountBlock.firstName){
        this.accountBlock.firstName='';
      }if(this.accountBlock.lastName){
        this.accountBlock.lastName='';
      }
      this.accountBlock.customerName = this.accountBlock.firstName + '' + this.accountBlock.lastName;
      this.accountBlock.customerNumber = params.customerNumber;
      this.accountBlock.accountNumber = params.accountNumber;

      this.accountBlock.branchCode = params.branchCode;
      this.accountBlock.mobileNo = params.mobileNo;
      this.accountBlock.emailAddress = params.email;
      this.data = params.date;
      // //console.log(this.accountBlock);
      // //console.log(this.accountBlock.customerName);

    });
    // //console.log(this.accountBlock.customerName);

  }

  onClickOfSubmit() {
    this.accountBlock.customerName = this.accountBlockForm.get('customerName').value;
    // console.log(  this.accountBlock.customerName );
    this.accountBlock.customerNumber = this.accountBlockForm.get('customerNumber').value;
    // console.log( this.accountBlock.customerNumber);
    this.sendMailtoOpt.emailid = this.accountBlockForm.get('emailid').value;
    this.sendMailtoOpt.nameOfCustomer = this.accountBlockForm.get('nameOfCustomer').value;
    this.sendMailtoOpt.mobileNumber = this.accountBlockForm.get('mobileNumber').value;
    this.sendMailtoOpt.dueDeligenceInfo = this.accountBlockForm.get('dueDeligenceInfo').value;
    this.sendMailtoOpt.commentsByOperator = this.accountBlockForm.get('commentsByOperator').value;
    this.sendMailtoOpt.reasonForBlocking = this.accountBlockForm.get('reasonForBlocking').value;
    this.sendMailtoOpt.nameOfOperator = this.accountBlockForm.get('nameOfOperator').value;
    this.accountBlock.branchCode = this.accountBlockForm.get('branchCode').value;
  //  //console.log(this.accountBlock);
    this.accountBlock.accountNumber = this.accountBlockForm.get('customerAccountNumber').value;
    this.accountBlock.mobileNo = this.accountBlockForm.get('customerMobileNumber').value;
    this.sendMailtoOpt.sendMailtoCustomer = this.accountBlockForm.get('sendmail').value;
    // console.log( this.sendMailtoOpt.sendMailtoCustomer);
    this.sendMailtoOpt.customerName = this.accountBlock.customerName;
    this.sendMailtoOpt.customerMobileNumber = this.accountBlock.mobileNo;
    this.sendMailtoOpt.branchCode = this.accountBlock.branchCode;
    this.sendMailtoOpt.customerNumber = this.accountBlock.customerNumber;
    this.sendMailtoOpt.customerAccountNumber = this.accountBlock.accountNumber;
    this.sendMailtoOpt.statusChangeDate = this.data;

    // console.log(this.sendMailtoOpt);
    // console.log(localStorage.getItem('currentUser'));
    this.currentUser = localStorage.getItem('currentUser');
    this.sendMailtoOpt.status = this.customerdatafromcomponent.accstatus;
    if (!this.sendMailtoOpt.sendMailtoCustomer) {
    // console.log('send mail to customer enable');
    this.sendMailtoOpt.sendMailtoCustomer = false;
    }
    //console.log(this.sendMailtoOpt);
    if (this.sendMailtoOpt.mobileNumber && this.sendMailtoOpt.nameOfCustomer && this.sendMailtoOpt.dueDeligenceInfo && this.sendMailtoOpt.reasonForBlocking && this.sendMailtoOpt.commentsByOperator) {
     this.submitdisable = true;
     this.accountBlockingService.accountBlock(this.sendMailtoOpt).subscribe(resp => {
        this.resp = resp;
        // console.log(resp);
        if (this.resp) {
          Swal.fire({
            title: 'Account frozen successfully.',

            // type: 'success',
          });

        } else {
          Swal.fire('Unable to process the request ');
        }
      });
     // console.log("Response from backend "+this.resp);
     this.navigatetoCustSearch();
// routerLink="/account-block/customer-search"

    } else {
      Swal.fire ('All fields are mandatory', 'Ensure all fields are captured correctly.');
    }
  }
navigatetoCustSearch() {
  // console.log('method nav call');
  this.router.navigate['/account-block/customer-search'];
}


}
