import { Component, OnInit, ViewChildren, QueryList } from '@angular/core';
import { MatPaginator } from '@angular/material';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-view-on-account-blocking',
  templateUrl: './view-on-account-blocking.component.html',
  styleUrls: ['./view-on-account-blocking.component.css']
})
export class ViewOnAccountBlockingComponent implements OnInit {

  displayedColumns: string[] = ['customerNo','accountNo','branchCode','transactionAmount','debitCredit','currency','transactionDescription'];
  
 

  @ViewChildren(MatPaginator) paginator= new QueryList<MatPaginator>();
  
  dataSource: any;
  emailMaintenace:FormGroup;
  

  constructor(private formBuilder:FormBuilder) {
   
  }

  ngOnInit() {
    this.emailMaintenace = this.formBuilder.group({
      emailid: ['', [Validators.required,Validators.email]]
    });

  }
 
  private fieldArray: Array<any> = [];
  private newAttribute: any = {};

  addFieldValue() {
      this.fieldArray.push(this.newAttribute)
      this.newAttribute = {};
  }

  deleteFieldValue(index) {
      this.fieldArray.splice(index, 1);
  }

}
