import { Component, OnInit, ViewChild, Input, ViewChildren, QueryList } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { AccountBlockingServiceService } from 'src/app/shared/services/account-blocking-service.service';
import { FCUBSAccsCustomerQueryResDTO } from './FCUBSAccsCustomerQueryResDTO';
import { MatTableDataSource, MatPaginator, MatDialog } from '@angular/material';
import { NavigationExtras, Router, ActivatedRoute } from '@angular/router';
import { ViewOnAccountBlockingComponent } from '../view-on-account-blocking/view-on-account-blocking.component';
import { AccountDetails } from './AccountDetails';
import { DataService } from './dataservice';
import * as CryptoJS from 'crypto-js';
@Component({
  selector: 'app-account-customer-search',
  templateUrl: './account-customer-search.component.html',
  styleUrls: ['./account-customer-search.component.css']
})
export class AccountCustomerSearchComponent implements OnInit {
  // respFromBackend = new FCUBSAccsCustomerQueryResDTO();
  showSpinn = false;
  fetch = false;
  checkCust: boolean;
  check: boolean;
  res: any;
  currentUser: string;
  viewTbale: any;
  showtable1: boolean;
  showtable2: boolean;
  freezebutton: boolean;
  currentdataforblock: any;
  //custMoreDetailsdata: Array<AccountDetails>;
  custMoreDetailsdata: Array<any>;
  constructor(private accountBlockingService: AccountBlockingServiceService,
    public dialog: MatDialog,
    private formBuild: FormBuilder,
    private route: Router,
    private dataService: DataService,
    private routevidya: ActivatedRoute) { }
  accountForm: FormGroup;

  result: any;
  showtable = false;
  displayedColumns: string[] = ['accountNumber', 'actionsColumn'];
  // 'accountNumber', 'branchCode', 'mobileNo'
  // displayedColumns1: string[] = ['accountCurrency','accountNumber','blockStatus','branchCode','customerAddress','customerName','customerNumber','dateOfBirth','emailAddress','fcubsCurrentDate','firstName','lastName','mobileNo','nationalId'];
  displayedColumns1: string[] = ['pnationalid', 'branchcode', 'custacno', 'ccy', 'custno', 'firstname', 'lastname', 'dateofbirth', 'email', 'addr1', 'frozen', 'noCredit', 'noDebit', 'dormant', 'actionsColumn'];

  displayedColumns2: string[] = ['refNo', 'accBranch', 'accNo', 'transDate', 'drBrIndicator', 'fCurrAmt', 'lCurrAmt'];
  dataSource1: any;
  dataSource2: any;
  dataSource = new MatTableDataSource();

  trndata: any;
  @ViewChildren(MatPaginator) paginator = new QueryList<MatPaginator>();
  user_id: any;
  data: string;
  ngOnInit() {

    this.accountForm = this.formBuild.group({
      firstName: [''],
      customerNumber: ['', [Validators.minLength(4), Validators.maxLength(9)]],
      accountNumber: ['', [Validators.minLength(10), Validators.maxLength(15)]],
      mobileNo: ['', [Validators.minLength(10), Validators.maxLength(15), Validators.pattern('^[0-9 \-\']+')]],
      lastName: [''],
      customerName: [''],
      branchCode: ['', [Validators.minLength(3), Validators.maxLength(3)]],
      nationalId: ['']
    });

    this.user_id = sessionStorage.getItem('user_id');
  }
  onFetch(formData) {
    this.dataSource = null;
    this.dataSource1 = null;
    this.dataSource2 = null;
    this.showtable = false;
    this.showtable1 = false;
    this.showtable2 = false;
    if (formData) {
      this.showSpinn = true;
      //new code for security
      this.data = JSON.stringify(formData);
      // console.log(this.data);
      var encrypted = CryptoJS.AES.encrypt(this.data, "@12#90!^*NPR*g&*()$34#$");

      var dataenc = encrypted.toString();
      console.log(dataenc);
      this.accountBlockingService.accountBlockingQuery(dataenc, this.user_id)
        .subscribe(result => {

          this.result = result;
          // console.log('Response for cust search ::' + this.result);
          if (this.result) {
            this.showSpinn = false;
            // console.log('Response from backend' + this.result);
            this.showtable = true;
            this.dataSource = new MatTableDataSource(
              this.result
            );
            //   this.dataSource.paginator = this.paginator.toArray()[0];



          } else {
            this.showSpinn = false;
            Swal.fire({

              text: 'Customer Not Found',
              type: 'warning',
            });
          }

        });

      this.fetch = true;
    } else {
      Swal.fire('Enter Customer Information ');
    }
  }
  custMoreDetails(email) {
    // console.log(email);
    this.currentdataforblock = email;
    this.showSpinn = true;
    this.accountBlockingService.custMoreDetails()
      .subscribe(custMoreDetailsdata => {

        this.custMoreDetailsdata = custMoreDetailsdata;
        //  console.log(this.custMoreDetailsdata);
        if (this.custMoreDetailsdata) {
          this.showSpinn = false;
          this.showtable1 = true;
          if (this.custMoreDetailsdata[0].frozen === 'Y') {
            this.freezebutton = true;
          }
          this.dataSource1 = new MatTableDataSource<AccountDetails>(this.custMoreDetailsdata);
          // console.log('Cust Details more from backend'+ this.custMoreDetailsdata);
        } else {
          Swal.fire('No Data For Customer More Details');
        }
      });
  }
  onClickOfBlock(row) {
    // console.log('block call');
    // console.log(row);
    this.dataService.setNewUserInfo(row);
    const navigationExtras: NavigationExtras = {
      queryParams: {
        firstname: row.firstname,
        lastname: row.lastname,
        customerNumber: row.custno,
        branchCode: row.branchcode,
        accountNumber: row.custacno,
        mobileNo: row.mobilenumber,
        email: row.email,
        date: this.currentdataforblock
      }

    };
    this.route.navigate(['account-block'], navigationExtras);


  }

  transactiondetails(row) {

    // get the backend dat for transaction
    // console.log(row);

    // branch code is missing

    // console.log(row.custacno);
    // console.log(row.branchcode);
    // console.log(row.custno);
    this.showSpinn = true;
    this.accountBlockingService.last10Transaction(row.custacno, row.branchcode, row.custno)
      .subscribe(trndata => {
        this.showSpinn = false;
        this.trndata = trndata;
        // console.log('Transaction Data from backend'+ this.trndata);
        if (this.trndata) {
          this.showtable2 = true;
          this.dataSource2 = new MatTableDataSource<any>(this.trndata);
        } else {
          Swal.fire('No data to display ');
        }
      });
  }

}
